#page-indications {
  display: flex;
}

#page-indications main {
  flex: 1;
}

.reports-boxes-content {
  width: 800px !important;
  margin: 64px auto;

  background: #ffffff;
  border: 1px solid #d3e2e5;
  border-radius: 20px;

  padding: 64px 50px;

  overflow: hidden;
}

form.filter-form {
  display: grid;
  grid-template-columns: 1fr 1fr;

  justify-content: center;

  margin-bottom: 64px;
}

form.filter-form .dates {
  display: grid;
  grid-template-columns: 1fr 1fr;

  row-gap: 5px;

  justify-content: center;
}

form.filter-form .input-block {
  display: flex;
  flex-direction: column;
  width: 100%;
  justify-self: center;
}

form.filter-form .input-block label {
  display: flex;
  color: #8fa7b3;
  margin-bottom: 8px;
  line-height: 24px;
}

form.filter-form .input-block label span {
  font-size: 14px;
  color: #8fa7b3;
  margin-left: 24px;
  line-height: 24px;
}

form.filter-form .input-block select {
  background: #f5f8fa;
  border: 1px solid #d3e2e5;
  outline: none;
  color: #5c8599;
  border-radius: 10px 10px 0px 0px;
  padding: 16px;
  line-height: 28px;
  outline: 0px;
  height: 64px;
}

form.filter-form .input-block .select-status option {
  color: black;
  background: #f5f8fa;
}

form.filter-form button.confirm-button {
  margin-top: 18px;

  width: 90%;
  height: 60px;
  border: 0;
  cursor: pointer;
  background: #3cdc8c;
  border-radius: 10px;
  color: #ffffff;
  font-weight: 800;

  display: flex;
  justify-content: center;
  align-items: center;
  justify-self: center;

  transition: background-color 0.2s;
  outline: none;
}

form.filter-form button.print-button {
  margin-top: 18px;

  width: 90%;
  height: 60px;
  border: 0;
  cursor: pointer;
  background: #15c3d6;
  border-radius: 10px;
  color: #ffffff;
  font-weight: 800;

  display: flex;
  justify-content: center;
  justify-self: center;
  align-items: center;

  transition: background-color 0.2s;
  outline: none;
}

form.filter-form button.print-button:disabled,
form.filter-form button.print-button[disabled] {
  border: 1px solid #d3e2e5;
  background-color: #cccccc;
  color: white;
  cursor: default;
}

form.filter-form button.confirm-button:hover {
  background: #36cf82;
}

form.filter-form .input-block .date-input {
  background: #f5f8fa;
  border: 1px solid #d3e2e5;
  outline: none;
  color: #5c8599;
  border-radius: 10px 10px 0px 0px;
  padding: 16px;
  outline: 0px;
  width: 95%;
}

.pagination {
  display: flex;
  justify-content: center;
  align-items: center;
}

@-webkit-keyframes rotating /* Safari and Chrome */ {
  from {
    -webkit-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
    -o-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes rotating {
  from {
    -ms-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -webkit-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  to {
    -ms-transform: rotate(360deg);
    -moz-transform: rotate(360deg);
    -webkit-transform: rotate(360deg);
    -o-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
.rotating {
  -webkit-animation: rotating 2s linear infinite;
  -moz-animation: rotating 2s linear infinite;
  -ms-animation: rotating 2s linear infinite;
  -o-animation: rotating 2s linear infinite;
  animation: rotating 2s linear infinite;
}

.rotate-box {
  margin-top: 60px;

  display: flex;
  justify-content: center;
  align-items: center;
}

.no-data-founded {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin: 0 auto;
  justify-content: center;
  width: 400px;
}

.no-data-founded span {
  color: black;
  margin-left: 40px;
}
