#page-landing {
  background: #EBF2F5;
  width: 100vw;
  height: 100vh;

  display: flex;
  justify-content: center;
  align-items: center;
}

#page-landing .content-wrapper {
  position: relative;

  width: 100vw;
  max-width: 1100px;
  height: 100vh;
  max-height: 680px;

  display: flex;
  align-items: flex-start;
  flex-direction: column;
  justify-content: space-between;
}

#page-landing .content-wrapper main h1 {
  max-width: 400px;
  font-size: 76px;
  color: #36CF82;
  font-weight: 900;
  line-height: 70px;
}

#page-landing .content-wrapper main p {
  max-width: 350px;
  margin-top: 40px;
  font-size: 24px;
  line-height: 34px;
}

.content-wrapper .location {
  position: absolute;
  right: 0;
  top: 0;

  font-size: 24px;
  line-height: 34px;
  
  display: flex;
  flex-direction: column;
}

#content-wrapper .location strong {
  font-weight: 800;
}

.content-wrapper .enter-app {
  position: absolute;
  right: 0;
  bottom: 0;

  width: 80px;
  height: 80px;
  
  background: #ffd666;
  border-radius: 30px;

  display: flex;
  align-items: center;
  justify-content: center;

  transition: background-color 0.2s;
}

.content-wrapper .enter-app:hover {
  background: #96FEFF;
}

.content-wrapper img {
  width: 350px;
}

form.indication-form {
  width: 500px;
  margin: 64px auto;

  background: #FFFFFF;
  border: 1px solid #D3E2E5;
  border-radius: 20px;

  padding: 64px 80px;

  overflow: hidden;
}

form.indication-form fieldset {
  border: 0;
}

form.indication-form fieldset + fieldset {
  margin-top: 80px;
}

form.indication-form fieldset legend {
  width: 100%;

  font-size: 32px;
  line-height: 34px;
  color: #3CDC8C;
  font-weight: 700;

  border-bottom: 1px solid #D3E2E5;
  margin-bottom: 40px;
  padding-bottom: 24px;

  text-align: center;
}

form.indication-form .input-block + .input-block {
  margin-top: 24px;
}

form.indication-form .input-block label {
  display: flex;
  color: #8FA7B3;
  margin-bottom: 8px;
  line-height: 24px;
}

form.indication-form .input-block label span {
  font-size: 14px;
  color: #8FA7B3;
  margin-left: 24px;
  line-height: 24px;
}

form.indication-form .input-block input,
form.indication-form .input-block textarea {
  width: 100%;
  background: #F5F8FA;
  border: 1px solid #D3E2E5;
  border-radius: 20px;
  outline: none;
  color: #5C8599;
}

form.indication-form .input-block input {
  height: 64px;
  padding: 0 16px;
}

form.indication-form .input-block textarea {
  min-height: 120px;
  max-height: 240px;
  resize: vertical;
  padding: 16px;
  line-height: 28px;
}

form.indication-form button.confirm-button {
  margin-top: 64px;

  width: 100%;
  height: 64px;
  border: 0;
  cursor: pointer;
  background: #3CDC8C;
  border-radius: 20px;
  color: #FFFFFF;
  font-weight: 800;

  display: flex;
  justify-content: center;
  align-items: center;

  transition: background-color 0.2s;
}

form.indication-form button.confirm-button:hover {
  background: #36CF82;
}