#page-users {
  display: flex;
}

#page-users main {
  flex: 1;
}

.indications-boxes-content {
  width: 700px;
  margin: 64px auto;

  background: #FFFFFF;
  border: 1px solid #D3E2E5;
  border-radius: 20px;

  padding: 64px 80px;

  overflow: hidden;
}

form.user-form {
  display: grid;
  grid-template-columns: 1fr 1fr;

  column-gap: 10px;
  row-gap: 5px;

  justify-content: center;

  margin-bottom: 64px;
}

form.user-form .dates {
  display: grid;
  grid-template-columns: 1fr 1fr;

  column-gap: 10px;
  row-gap: 5px;

  justify-content: center;
}

form.user-form .input-block {
  display: flex;
  flex-direction: column;
}

form.user-form .input-block label {
  display: flex;
  color: #8FA7B3;
  margin-bottom: 8px;
  line-height: 24px;
}

form.user-form .input-block label span {
  font-size: 14px;
  color: #8FA7B3;
  margin-left: 24px;
  line-height: 24px;
}

form.user-form .input-block select {
  background: #F5F8FA;
  border: 1px solid #D3E2E5;
  outline: none;
  color: #5C8599;
  border-radius: 10px 10px 0px 0px;
  padding: 16px;
  line-height: 28px;
  outline:0px;
}

form.user-form .input-block .select-status option {
  color: black;
  background: #F5F8FA;
}

form.user-form button.confirm-button {
  margin-top: 18px;

  width: 100%;
  height: 60px;
  border: 0;
  cursor: pointer;
  background: #3CDC8C;
  border-radius: 10px;
  color: #FFFFFF;
  font-weight: 800;

  display: flex;
  justify-content: center;
  align-items: center;

  transition: background-color 0.2s;
  outline: 0;
}

.add-button {
  margin-bottom: 18px;

  width: 30%;
  height: 60px;
  border: 0;
  cursor: pointer;
  background: #3CDC8C;
  border-radius: 10px;
  color: #FFFFFF;
  font-weight: 800;
  padding: 15px;

  display: flex;
  justify-content: space-between;
  align-items: center;

  transition: background-color 0.2s;
  outline: none;
}

form.user-form button.print-button {
  margin-top: 18px;

  width: 100%;
  height: 60px;
  border: 0;
  cursor: pointer;
  background: #15C3D6;
  border-radius: 10px;
  color: #FFFFFF;
  font-weight: 800;

  display: flex;
  justify-content: center;
  align-items: center;

  transition: background-color 0.2s;
  outline: none;
}

form.user-form button.print-button:disabled,
form.user-form button.print-button[disabled] { 
  border: 1px solid #D3E2E5;
  background-color: #cccccc;
  color: white;
  cursor: default;
}

form.user-form button.confirm-button:hover {
  background: #36CF82;
}

form.user-form .input-block .date-input {
  background: #F5F8FA;
  border: 1px solid #D3E2E5;
  outline: none;
  color: #5C8599;
  border-radius: 10px 10px 0px 0px;
  padding: 16px;
  outline:0px;
}

.pagination {
  display: flex;
  justify-content: center;
  align-items: center;
}

@-webkit-keyframes rotating /* Safari and Chrome */ {
  from {
    -webkit-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
    -o-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes rotating {
  from {
    -ms-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -webkit-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  to {
    -ms-transform: rotate(360deg);
    -moz-transform: rotate(360deg);
    -webkit-transform: rotate(360deg);
    -o-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
.rotating {
  -webkit-animation: rotating 2s linear infinite;
  -moz-animation: rotating 2s linear infinite;
  -ms-animation: rotating 2s linear infinite;
  -o-animation: rotating 2s linear infinite;
  animation: rotating 2s linear infinite;
}

.rotate-box {
  margin-top: 60px;

  display: flex;
  justify-content: center;
  align-items: center;
}

.not-same-password {
  display: flex;
  align-items: center;
  justify-content: space-between;

  padding: 10px 0px;
  width: 70%;
  color: red;
}

form.update-user-form button.confirm-button:disabled,
form.update-user-form button.confirm-button[disabled] { 
  border: 1px solid #D3E2E5;
  background-color: #cccccc;
  color: white;
  cursor: default;
}