
aside.sidebar {
  width: 440px;
  height: 100vh;
  background: #03BE52;
  padding: 80px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

aside.sidebar h2 {
  font-size: 40px;
  font-weight: 800;
  line-height: 42px;
  margin-top: 64px;
}

aside.sidebar p {
  line-height: 28px;
  margin-top: 24px;
}

aside.sidebar img {
  width: 250px;
}

ul li span {
  margin-left: 15px;
  font-size: 24px;
}

ul li {
  list-style: none;
  display: flex;
  align-items: center;
  margin-bottom: 16px;
  transition: all 0.2s;
}

footer li {
  list-style: none;
  display: flex;
  align-items: center;
  transition: all 0.2s;
}

li:hover {
  padding-left: 5px;
  color: #C4c4c4;
  cursor: pointer;
}

footer li span {
  margin-left: 15px;
  font-size: 24px;
}

footer {
  display: flex;
  flex-direction: column;

  line-height: 24px;
}

footer strong {
  font-weight: 800;
}

.list-sidebar {
  margin-top: 60px;
}