.main-recover {
  width: 100%;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.box-center-recover {
  border-radius: 5px;
  max-width: 500px;
  transition: all 2s ease;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

form.recover-password {
  width: 500px;
  margin: 64px auto;

  background: #FFFFFF;
  border: 1px solid #D3E2E5;
  border-radius: 20px;

  padding: 64px 80px;

  overflow: hidden;
}

form.recover-password fieldset {
  border: 0;
  margin-bottom: 20px;
}

form.recover-password fieldset + fieldset {
  margin-top: 80px;
}

form.recover-password fieldset legend {
  width: 100%;

  font-size: 32px;
  line-height: 34px;
  color: #3CDC8C;
  font-weight: 700;

  border-bottom: 1px solid #D3E2E5;
  margin-bottom: 40px;
  padding-bottom: 24px;

  text-align: center;
}

form.recover-password .input-block + .input-block {
  margin-top: 24px;
}

form.recover-password .input-block label {
  display: flex;
  color: #8FA7B3;
  margin-bottom: 8px;
  line-height: 24px;
}

form.recover-password .input-block label span {
  font-size: 14px;
  color: #8FA7B3;
  margin-left: 24px;
  line-height: 24px;
}

form.recover-password .input-block input,
form.recover-password .input-block textarea {
  width: 100%;
  background: #F5F8FA;
  border: 1px solid #D3E2E5;
  border-radius: 20px;
  outline: none;
  color: #5C8599;
}

form.recover-password .input-block input {
  height: 64px;
  padding: 0 16px;
}

form.recover-password .input-block textarea {
  min-height: 120px;
  max-height: 240px;
  resize: vertical;
  padding: 16px;
  line-height: 28px;
}

form.recover-password button.confirm-button {
  margin-top: 64px;

  width: 100%;
  height: 64px;
  border: 0;
  cursor: pointer;
  background: #3CDC8C;
  border-radius: 20px;
  color: #FFFFFF;
  font-weight: 800;

  display: flex;
  justify-content: center;
  align-items: center;

  transition: background-color 0.5s;
}

form.recover-password button.confirm-button:hover {
  background: #36CF82;
}

form.recover-password button.confirm-button:disabled {
  background-color: #ccc;
}

.message-error {
  color: red;
  padding: 30px 0px;
}

@-webkit-keyframes rotating /* Safari and Chrome */ {
  from {
    -webkit-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
    -o-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes rotating {
  from {
    -ms-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -webkit-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  to {
    -ms-transform: rotate(360deg);
    -moz-transform: rotate(360deg);
    -webkit-transform: rotate(360deg);
    -o-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
.rotating {
  -webkit-animation: rotating 2s linear infinite;
  -moz-animation: rotating 2s linear infinite;
  -ms-animation: rotating 2s linear infinite;
  -o-animation: rotating 2s linear infinite;
  animation: rotating 2s linear infinite;
}

@media (max-width: 500px) {
  .box-center-recover {
    width: 100%;
    max-width: 400px;
  }

  form.recover-password {
    padding: 64px 30px;
  }
}