#page-indications {
  display: flex;
}

#page-indications main {
  flex: 1;
}

.indications-boxes-content {
  width: 700px;
  margin: 64px auto;

  background: #ffffff;
  border: 1px solid #d3e2e5;
  border-radius: 20px;

  padding: 64px 80px;

  overflow: hidden;
}

form.filter-form {
  display: grid;
  grid-template-columns: 1fr 1fr;

  column-gap: 10px;
  row-gap: 5px;

  justify-content: center;

  margin-bottom: 20px;
}

form.filter-form .input-block {
  display: flex;
  flex-direction: column;
}

form.filter-form .input-block label {
  display: flex;
  color: #8fa7b3;
  margin-bottom: 8px;
  line-height: 24px;
}

form.filter-form .input-block label span {
  font-size: 14px;
  color: #8fa7b3;
  margin-left: 24px;
  line-height: 24px;
}

form.filter-form .input-block select {
  background: #f5f8fa;
  border: 1px solid #d3e2e5;
  outline: none;
  color: #5c8599;
  border-radius: 10px 10px 0px 0px;
  padding: 16px;
  line-height: 28px;
  outline: 0px;
}

form.filter-form .input-block input {
  background: #f5f8fa;
  border: 1px solid #d3e2e5;
  outline: none;
  color: #5c8599;
  border-radius: 10px;
  padding: 16px;
  line-height: 28px;
  outline: 0px;
}

form.filter-form .input-block .select-status option {
  color: black;
  background: #f5f8fa;
}

form.filter-form button.confirm-button-filter {
  margin-top: 32px;

  width: 100%;
  height: 60px;
  border: 0;
  cursor: pointer;
  background: #3cdc8c;
  border-radius: 10px;
  color: #ffffff;
  font-weight: 800;

  display: flex;
  justify-content: center;
  align-items: center;

  transition: background-color 0.2s;
  outline: none;
}

form.filter-form button.print-button {
  margin-top: 32px;

  width: 100%;
  height: 60px;
  border: 0;
  cursor: pointer;
  background: #15c3d6;
  border-radius: 10px;
  color: #ffffff;
  font-weight: 800;

  display: flex;
  justify-content: center;
  align-items: center;

  transition: background-color 0.2s;
  outline: none;
}

form.filter-form button.print-button:disabled,
form.filter-form button.print-button[disabled] {
  border: 1px solid #d3e2e5;
  background-color: #cccccc;
  color: white;
  cursor: default;
}

form.filter-form button.confirm-button-filter:hover {
  background: #36cf82;
}

form.create-orphanage-form .input-block .button-select {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

form.create-orphanage-form .input-block .button-select button {
  height: 64px;
  background: #f5f8fa;
  border: 1px solid #d3e2e5;
  color: #5c8599;
  cursor: pointer;
}

form.create-orphanage-form .input-block .button-select button.active {
  background: #edfff6;
  border: 1px solid #a1e9c5;
  color: #37c77f;
}

form.create-orphanage-form .input-block .button-select button:first-child {
  border-radius: 20px 0px 0px 20px;
}

form.create-orphanage-form .input-block .button-select button:last-child {
  border-radius: 0 20px 20px 0;
  border-left: 0;
}

form.create-orphanage-form button.confirm-button-filter {
  margin-top: 64px;

  width: 100%;
  height: 64px;
  border: 0;
  cursor: pointer;
  background: #3cdc8c;
  border-radius: 20px;
  color: #ffffff;
  font-weight: 800;

  display: flex;
  justify-content: center;
  align-items: center;

  transition: background-color 0.2s;
}

form.create-orphanage-form button.confirm-button-filter svg {
  margin-right: 16px;
}

form.create-orphanage-form button.confirm-button-filter:hover {
  background: #36cf82;
}

.pagination {
  display: flex;
  justify-content: center;
  align-items: center;
}

@-webkit-keyframes rotating /* Safari and Chrome */ {
  from {
    -webkit-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
    -o-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes rotating {
  from {
    -ms-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -webkit-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  to {
    -ms-transform: rotate(360deg);
    -moz-transform: rotate(360deg);
    -webkit-transform: rotate(360deg);
    -o-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
.rotating {
  -webkit-animation: rotating 2s linear infinite;
  -moz-animation: rotating 2s linear infinite;
  -ms-animation: rotating 2s linear infinite;
  -o-animation: rotating 2s linear infinite;
  animation: rotating 2s linear infinite;
}

.rotate-box {
  margin-top: 60px;

  display: flex;
  justify-content: center;
  align-items: center;
}

.no-data-founded {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin: 0 auto;
  justify-content: center;
  width: 400px;
}

.no-data-founded span {
  color: black;
  margin-left: 40px;
}
