.export-button {
  margin-top: 18px;

  width: 90%;
  height: 60px;
  border: 0;
  cursor: pointer;
  background: #1F7349;
  border-radius: 10px;
  color: #FFFFFF;
  font-weight: 800;

  display: flex;
  justify-content: center;
  justify-self: center;
  align-items: center;

  transition: background-color 0.2s;
  outline: none;
}