* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.not-found-page {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100vw;
  height: 100vh;
}

.not-found-page span {
  color: black;
  cursor: pointer;

  font-size: 48px;
  margin-top: 70px;
}