#page-map {
  display: flex;
}

#page-map main {
  flex: 1;
}

#page-map main .content {
  display: flex;
  max-width: 700px;
  margin: 64px auto;

  flex-wrap: wrap;
}

#page-map main .content .totality-indications {
  width: 220px;
  background: white;
  border-radius: 5px;

  padding: 5px 15px;
  margin: 5px;

  line-height: 42px;
  display: flex;
  flex-direction: column;
}

#page-map main .content .totality-indicators {
  width: 220px;
  background: #606062;
  border-radius: 5px;

  padding: 5px 15px;
  margin: 5px;

  line-height: 42px;
  display: flex;
  flex-direction: column;

  color: white;
}

#page-map main .content .totality-indications h4 {
  font-size: 24px;
}

#page-map main .content .totality-indications {
  color: black;
}

#page-map main .content .totality-indications span {
  color: black;
  font-weight: bold;
}

#page-map main .content .totality-indications .progress {
  color: #706dff;
}

#page-map main .content .totality-indications .finished {
  color: #03be52;
}

#page-map main .content .totality-indications .canceled {
  color: #c10000;
}

#page-map main .content .totality-indications span {
  font-size: 20px;
}

.graphs {
  display: flex;
  align-items: center;
}

.input-block {
  display: flex;
  flex-direction: column;
  width: 100%;
  justify-self: center;
}

.input-block label {
  display: flex;
  color: #8fa7b3;
  margin-bottom: 8px;
  line-height: 24px;
}

.input-block label span {
  font-size: 14px;
  color: #8fa7b3;
  margin-left: 24px;
  line-height: 24px;
}

.input-block select {
  background: #f5f8fa;
  border: 1px solid #d3e2e5;
  outline: none;
  color: #5c8599;
  border-radius: 10px 10px 0px 0px;
  padding: 16px;
  line-height: 28px;
  outline: 0px;
  width: 250px;
  margin-bottom: 40px;
}

.observation {
  padding-top: 40px;
  color: #666;
  font-style: italic;
}
