.user-box-box {
  background: #F7F7F7;
  padding: 10px 30px;

  border-radius: 10px;

  display: flex;
  flex-direction: row;

  justify-content: space-between;
  align-items: center;

  margin-bottom: 20px;
}

.user-box-user-information {
  display: flex;
  flex-direction: column;

  color: black;
}

.user-box-user-information h4 {
  font-size: 20px;
  font-weight: bold;

  margin-bottom: 10px;
  width: 250px;
}
.user-box-educational-information {
  display: flex;
  flex-direction: column;

  color: black;
}

.user-box-educational-information h4 {
  font-size: 18px;
}

.user-box-buttons {
  display: flex;
  flex-direction: row;
}

.user-box-buttons button {
  width: 48px;
  height: 48px;

  border: none;

  margin: 4px;
  background: #3CDC8C;
  border-radius: 16px;

  cursor: pointer;

  transition: background-color 0.2s;

  display: flex;
  justify-content: center;
  align-items: center;
  
  outline:none;
}

.user-box-buttons .edit-button:hover {
  background: #36CF82;
  overflow: hidden;
}

.user-box-buttons .trash-button:hover {
  background: rgb(252, 88, 88);
  overflow: hidden;
}

.user-box-buttons .trash-button {
  background: red;
  overflow: hidden;
}

.modal-body {
  width: 350px;
  background-color: #FFFFFF;
  outline: 0;
  border-radius: 15px;
  color: black;
  padding: 10px 20px;
}

.modal-body h3 {
  margin-bottom: 20px;
  text-align: center;

  font-weight: 800;
  font-size: 28px;
}

.modal-body fieldset {
  border: 0;
  padding-top: 10px;
}

.modal-body fieldset + fieldset {
  margin-top: 25px;
}

.modal-body fieldset legend {
  width: 100%;

  font-size: 24px;

  border-bottom: 1px solid #D3E2E5;
  padding-bottom: 8px;

}

form.update-user-form .input-block + .input-block {
  margin-top: 24px;
}

form.update-user-form .input-block label {
  display: flex;
  color: #8FA7B3;
  margin-bottom: 8px;
  line-height: 24px;
}

form.update-user-form .input-block label span {
  font-size: 14px;
  color: #8FA7B3;
  margin-left: 24px;
  line-height: 24px;
}

form.update-user-form .input-block input,
form.update-user-form .input-block textarea,
form.update-user-form .input-block select {
  width: 100%;
  background: #F5F8FA;
  border: 1px solid #D3E2E5;
  outline: none;
  color: #5C8599;
}


form.update-user-form .input-block textarea {
  border-radius: 0px 0px 10px 10px;
  min-height: 120px;
  max-height: 240px;
  resize: vertical;
  padding: 16px;
  line-height: 28px;
}

form.update-user-form .input-block select {
  border-radius: 10px 10px 0px 0px;
  padding: 16px;
  line-height: 28px;
  outline:0px;
}

form.update-user-form .input-block input {
  border-radius: 10px;
  padding: 16px;
  line-height: 28px;
  outline:0px;
}

form.update-user-form .input-block .select-status option {
  color: black;
  background: #F5F8FA;
}


form.update-user-form button.confirm-button {
  margin-top: 32px;

  width: 100%;
  height: 64px;
  border: 0;
  cursor: pointer;
  background: #3CDC8C;
  border-radius: 20px;
  color: #FFFFFF;
  font-weight: 800;

  display: flex;
  justify-content: center;
  align-items: center;

  transition: background-color 0.2s;
}

form.update-user-form button.confirm-button:hover {
  background: #36CF82;
}

.buttons-delete-modal {
  display: flex;
  flex-direction: row;
}

.buttons-delete-modal button {
  margin: 4px;
  outline:0px;
}

.buttons-delete-modal .delete-button {
  margin-top: 32px;

  width: 100%;
  height: 64px;
  border: 0;
  cursor: pointer;
  background: #3CDC8C;
  border-radius: 20px;
  color: #FFFFFF;
  font-weight: 800;

  display: flex;
  justify-content: center;
  align-items: center;

  transition: background-color 0.2s;
}

.buttons-delete-modal .cancel-button {
  margin-top: 32px;

  width: 100%;
  height: 64px;
  border: 0;
  cursor: pointer;
  background: red;
  border-radius: 20px;
  color: #FFFFFF;
  font-weight: 800;

  display: flex;
  justify-content: center;
  align-items: center;

  transition: background-color 0.2s;
}