.nav {
  display: flex;
  flex-direction: row;

  max-width: 500px;

  flex-wrap: wrap;

  justify-content: center;
  row-gap: 5px;
}

.nav-link {
  border: 0.5px solid #3CDC8C;
  padding: 3px 10px;
  border-radius: 2px;
  list-style: none;
  text-decoration: none;
  color: #3CDC8C;
  transition: 0.3s all;
  margin: 0 3px;
}

.nav-link:hover {
  background: #3CDC8C;
  color: white;
  cursor: pointer;
}